.movie_box {
  display: flex;
  border-bottom: 1px solid #888;
}
.info_box {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
}

.button {
  display: inline-block;
  text-decoration: none;
  color: #000;
  padding: 6px;
  border: 1px solid #dfdcdc;
  margin-bottom: 4px;
}

.additional {
  padding: 16px;
  border-bottom: 1px solid #888;
}

.additional_list {
  padding: 16px;
  padding-bottom: 0;
}
